import { MDCFloatingLabel } from '@material/floating-label';
import clsx from 'clsx';
import React, {
    useEffect,
    useRef,
    forwardRef,
    useImperativeHandle
} from 'react';
import {
    FloatingLabelDefaultProps,
    FloatingLabelPropTypes
} from './generated/propTypes';

const FloatingLabel = forwardRef((props, externalRef) => {
    const {
        children,
        htmlFor = '',
        shake = false,
        float = false,
        className,
        style,
        ...customProps
    } = props;
    const mdcFloatingLabelRef = useRef();
    const content = children;
    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    useEffect(() => {
        mdcFloatingLabelRef.current = MDCFloatingLabel.attachTo(ref.current);
        return () => {
            mdcFloatingLabelRef.current.destroy();
        };
    });
    useEffect(() => {
        mdcFloatingLabelRef.current.float(float);
    }, [float]);

    return (
        <label
            ref={ref}
            htmlFor={htmlFor}
            className={clsx(
                'lmnt',
                'mdc-floating-label',
                float && 'mdc-floating-label--float-above',
                shake && 'mdc-floating-label--shake',
                className
            )}
            {...customProps}
            style={style}
        >
            {content}
        </label>
    );
});

FloatingLabel.displayName = 'FloatingLabel';
FloatingLabel.propTypes = FloatingLabelPropTypes;
FloatingLabel.defaultProps = FloatingLabelDefaultProps;

export default FloatingLabel;
